.video-presentation {
  padding: 17px 26px 0 28px;
  background-color: @white;
  font-family: "PT Sans", "Arial", sans-serif;
}

.video-presentation__container {
  margin-bottom: 38px;
}

.video-presentation__slogan {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  color: @black;
}

.video-presentation__description {
  margin: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  text-transform: uppercase;
  color: @darkgray;
}
